import React, { useEffect, useState } from "react";

import { Button } from "@material-ui/core";
import CloudUploadIcon from "@material-ui/icons/CloudUpload";
import DeployModal from "./DeployModal";
import { useDispatch } from "react-redux";
import { requestGitUrl } from "../redux/actions/actions";
const RightNav = () => {
  const [open, setOpen] = useState(false);
  const dispatch = useDispatch()
  useEffect(() => {
    dispatch(requestGitUrl())
  }, [])
  return (
    <>
      <div
        className="position-fixed row"
        id="port-rightnav"
        style={{ top: "40px", right: "60px" }}
      >
        <div
          style={{ borderBottomLeftRadius: "10px" }}
          className="col-9 offset-3 top-label p-2 px-3"
        >
          Use you git to deploy it
        </div>
        <div className="text-center col-12 my-3">
          <div className="my-2">Make Request To Deploy Portfolio</div>
          <Button startIcon={<CloudUploadIcon />} onClick={()=>{setOpen(!open)}} className="bg-primary px-4">
            Deploy
          </Button>
        </div>
      </div>
      <DeployModal open={open} setOpen={setOpen} />
    </>
  );
};
export default RightNav;
