import { Button, Divider, Fab, Grid, Tooltip } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import ShowProject from "../../components/ShowProject";
import AddIcon from "@material-ui/icons/Add";
import AddProjectForm from "./AddProjectForm";
import { useDispatch, useSelector } from "react-redux";
import SaveIcon from "@material-ui/icons/Save";
import { requestSaveProjects } from "../../redux/actions/actions";

const ProjectForm = () => {
  const projects = useSelector((state) => state.user.data.projects);
  const dispatch = useDispatch();
  const [showProjectForm, setShowProjectForm] = useState(false);
  const saveProjects = () => {
    dispatch(requestSaveProjects(projects));
  };
  return (
    <>
      <Grid container item xs={11}>
        <Grid
          item
          container
          xs={11}
          md={6}
          className="mx-auto border rounded p-3 my-3"
          id="port-project"
        >
          <Grid item container xs={12} className="m-3 h2 ">Projects</Grid>
          <Grid item container xs={12}>
            {console.log("Proejct: " ,projects)}
            {projects.length>0 ? (
              projects.map(({ image, title, content, tech, url,_id }, index) => (
                <>
                  <ShowProject
                    _id={_id}
                    key={_id}
                    image={image}
                    title={title}
                    content={content}
                    tech={tech}
                    url={url}
                  />
                </>
              ))
            ) : (
              <p className="mx-auto">No Projects Found add some projects</p>
            )}
            <Divider style={{ width: "90%" }} className="bg-dark mx-auto" />
          </Grid>
          {showProjectForm ? (
            <AddProjectForm
              projects={projects}
              show={showProjectForm}
              setShow={setShowProjectForm}
            />
          ) : (
            <Grid
              container
              item
              xs={12}
              direction="row"
              justify="space-between"
              alignItems="center"
              className="px-4 my-3"
            >
              <Grid item>Add Project</Grid>
              <Grid item>
                <Tooltip title="Add Project">
                  <Fab
                    size="small"
                    color="primary"
                    aria-label="add"
                    onClick={() => {
                      setShowProjectForm(!showProjectForm);
                    }}
                  >
                    <AddIcon />
                  </Fab>
                </Tooltip>
              </Grid>
            </Grid>
          )}
            {projects.length>0 ? (
          <Grid item xs={12} className="px-3" style={{textAlign:"right"}}>
              <Button
                color="primary"
                variant="contained"
                size="small"
                startIcon={<SaveIcon />}
                onClick={saveProjects}
              >
                Save
              </Button>
          </Grid>
            ) : (
<></>
            )}
        </Grid>
      </Grid>
    </>
  );
};
export default ProjectForm;
